
.passage {
  position: relative;
  width: 100%;
  height: 100%;
  padding-top: 90px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background: #8c5d2c;
  -webkit-perspective: 300;
  text-align: center;
}
.passage a {
  color: #8c5d2c;
  text-decoration: none;
}
.passage .content {
  width: 100%;
  height: 100%;
  background: #fffde6;
  position: relative;
  margin: 0 auto;
  padding-top: 100px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.passage .table:before,
.passage .table:after,
.passage .content:before,
.passage .content:after {
  width: 10px;
  content: '';
  position: absolute;
  background: #8c5d2c;
  z-index: 10;
  top: 0px;
}
.passage .content:before,
.passage .content:after {
  -webkit-animation: move1 5s infinite ease-in-out;
}
@-webkit-keyframes move1 {
0% {
    -webkit-transform: rotateX(-15deg);
    height: 110px;
}
25% {
    top: -10;
    height: 110px;
    -webkit-transform: rotateX(0deg);
}
50% {
    top: -20px;
    height: 150px;
    -webkit-transform: rotateX(15deg);
}
75% {
    height: 110px;
    -webkit-transform: rotateX(0deg);
}
100% {
    height: 110px;
    -webkit-transform: rotateX(-15deg);
}
}
@keyframes move1 {
0% {
    -webkit-transform: rotateX(-15deg);
    height: 110px;
}
25% {
    top: -10;
    height: 110px;
    -webkit-transform: rotateX(0deg);
}
50% {
    top: -20px;
    height: 150px;
    -webkit-transform: rotateX(15deg);
}
75% {
    height: 110px;
    -webkit-transform: rotateX(0deg);
}
100% {
    height: 110px;
    -webkit-transform: rotateX(-15deg);
}
}
.passage .content:before {
  z-index: 1;
  left: 26%;
}
.passage .content:after {
  z-index: 1;
  left: 74%;
}
.passage .table {
  width: 60%;
  min-width: 300px;
  padding: 30px 20px 30px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0px auto;
  border: 1px solid #ECDAC4;
  -webkit-box-shadow: 1px 1px 5px 0px #fbc5aa inset;
          box-shadow: 1px 1px 5px 0px #fbc5aa inset;
  position: relative;
  z-index: 100;
  background: #FFFFE5;
  -webkit-animation: move 5s infinite ease-in-out;
}
@-webkit-keyframes move {
0% {
    -webkit-transform: rotateX(5deg);
}
25% {
    -webkit-transform: rotateX(0deg);
}
50% {
    -webkit-transform: rotateX(-5deg);
}
75% {
    -webkit-transform: rotateX(0deg);
}
100% {
    -webkit-transform: rotateX(5deg);
}
}
@keyframes move {
0% {
    -webkit-transform: rotateX(5deg);
}
25% {
    -webkit-transform: rotateX(0deg);
}
50% {
    -webkit-transform: rotateX(-5deg);
}
75% {
    -webkit-transform: rotateX(0deg);
}
100% {
    -webkit-transform: rotateX(5deg);
}
}
.passage .table:before,
.passage .table:after {
  width: 20px;
  height: 20px;
  border-radius: 10px;
  background: #EC745F;
  -webkit-box-shadow: 1px 1px 1px 1px #a1a3a1;
          box-shadow: 1px 1px 1px 1px #a1a3a1;
  top: 5px;
}
.passage .table:before {
  left: 9%;
  z-index: 10;
}
.passage .table:after {
  left: 90%;
}
.passage .table > div {
  height: 35px;
  line-height: 35px;
  text-align: left;
  border-bottom: 1px dashed #F4CCA0;
}



